import global from './../global'
cc.Class({
    extends: cc.Component,

    properties: {
        body: {
            default: null,
            type: cc.Node
        },
        jumpAudio:cc.AudioClip,
        attackAudio:cc.AudioClip,
        eatAudio:cc.AudioClip,
        beAttacked:cc.AudioClip,
        backgroundMusic:cc.AudioClip,
        speeds:[cc.Float],
        speedLevel:0,
        uiSpeed:cc.ProgressBar
    },

    onLoad(){
        cc.audioEngine.play(this.backgroundMusic,true,1);
        this.node.on('jump',this.onJump.bind(this));
        global.account.gameCtl.onTipsBegan(()=>{
           //
            console.log('');
            // this.node.emit('idle');
            // this.node.emit('time-scale');

        });
        global.account.gameCtl.onTipsEnd((data)=>{
            console.log('' + JSON.stringify(data));
            this.tipsResult = data.result;
            this.node.emit('attack');
            this.node.emit('time-scale', 1);
        });
        this.node.zIndex = 1;
        this.node.on('jump', this.onJump.bind(this));


        global.account.gameCtl.onTimeScale(()=>{
           this.node.emit('time-scale',defines.timeScale);
        });

        global.account.gameCtl.onPause(()=>{
            this.node.emit('time-scale',0);
        });
        global.account.gameCtl.onResume(()=>{

            this.node.emit('time-scale',defines.timeScale);
        });
        global.account.playerData.playerNode = this.node;
    },
    start(){
        
    },
    getNowSpeed() {
        return this.speeds[this.speedLevel];
    },

    onJump(event){
        console.log("on jump audio");
        let com=this.node.getComponent("jump");
        com.playJumpSound(this.jumpAudio);
    },

    onCollisionEnter: function (other, self) {
        // console.log('on collision enter');
        // other.node.removeComponent(cc.BoxCollider);
        //
        // if (this.tipsResult === 'success'){
        //     // this.tipsResult = undefined;
        //     if (other.getComponent('enemy')){
        //         cc.audioEngine.play(this.attackAudio,false,1);
        //         other.node.emit('fly');
        //         // this.node.emit('walk');
        //         setTimeout(()=>{
        //             this.node.emit('walk');
        //             global.account.gameCtl.attackEnd();
        //         },1);
        //         // this.node.emit('walk');
        //         // global.account.gameCtl.attackEnd();
            
        //         this.speedLevel++;
        //         this.speedLevel=Math.min(this.speedLevel,this.speeds.length-1);
        //         self.node.emit("time-scale",this.speeds[this.speedLevel]/this.speeds[0]);
        //         global.account.gameCtl.event.fire("speed-changed",{currentLevel:this.speedLevel,maxLevel:this.speeds.length-1,speedValue:this.speeds[this.speedLevel]});
        //     }
        // }
            if (other.getComponent('enemy')){
                cc.audioEngine.play(this.attackAudio,false,1);
                other.node.emit('fly');
                // this.node.emit('walk');
                setTimeout(()=>{
                    this.node.emit('walk');
                },1);
                // this.node.emit('walk');
                // global.account.gameCtl.attackEnd();
            
                this.speedLevel++;
                this.speedLevel=Math.min(this.speedLevel,this.speeds.length-1);
                self.node.emit("time-scale",this.speeds[this.speedLevel]/this.speeds[0]);
                global.account.gameCtl.event.fire("speed-changed",{currentLevel:this.speedLevel,maxLevel:this.speeds.length-1,speedValue:this.speeds[this.speedLevel]});
            }
        if(other.getComponent("stub-tag")){
            console.log("blink");
            this.speedLevel--;
            this.speedLevel=Math.max(this.speedLevel,0);
            this.changeSpeed();
            cc.audioEngine.play(this.beAttacked,false,1);
            let animate=this.body.getComponent(cc.Animation);
            animate.play("be-attacked");
        }

        if (other.getComponent('coin')){
            cc.audioEngine.play(this.eatAudio,false,1);
            other.node.emit('fly');
            global.account.playerData.goldCount ++;
        }

    },
    changeSpeed(){
        this.node.emit("time-scale",this.speeds[this.speedLevel]/this.speeds[0]);
        global.account.gameCtl.event.fire("speed-changed",{currentLevel:this.speedLevel,maxLevel:this.speeds.length-1,speedValue:this.speeds[this.speedLevel]});
    },
    onCollisionStay: function (other, self) {
        // console.log('on collision stay');
    },
    onCollisionExit: function (other, self) {
        // console.log('on collision exit');
    },



    start () {

    }
});
