    initPool() {
        global.poolManager.createPool(defines.poolNodeType.coin ,this.prizePrefab, 6);
        global.poolManager.createPool(defines.poolNodeType.stub, this.stubPrefab,6);
        global.poolManager.createPool(defines.poolNodeType.enemy, this.enemyPrefabList[0],6);

    },
    addLevelNode(){

        if (this.levelNodeIndex >= this.levelConfig.length){

            //todo  
            this.levelNodeIndex = 0;


            return;
        }
        let config = this.levelConfig[this.levelNodeIndex];
        console.log('config = ' + JSON.stringify(config));
        let type = config.type;
        let node = global.poolManager.getNode(type);
        node.type = type;
        node.parent = this.gameNode;
        node.emit('init', {
            playerNode: this.playerNode,
            x: cc.winSize.width * 0.5 + node.width * 0.5,
            y: config.y,
            distance: config.distance
        });
        this.levelNodeList.push(node);
        this.levelNodeIndex ++;
    },
    update(dt) {
        //
        if (this.levelConfig){
            if (this.levelNodeList.length === 0){
                this.addLevelNode();
            }else {
                let maxX = -20000;
                let maxNode = undefined;
                for (let i = 0 ; i < this.levelNodeList.length ; i ++){
                    let node = this.levelNodeList[i];
                    if (node.x > maxX){
                        maxX = node.x;
                        maxNode = node;
                    }
                }
                let distance = maxNode.getComponent('move').distance;
                if ((cc.winSize.width * 0.5 - (maxNode.width * 0.5 + maxX)) > distance){
                    this.addLevelNode();
                }
            }
        }
        for (let i = 0 ; i < this.levelNodeList.length ; i ++){
            let node = this.levelNodeList[i];
            if (node.dead === true){
                this.levelNodeList.splice(i, 1);
                global.poolManager.putNode(node.type, node);
            }
        }
    }

