import global from '../../global'
cc.Class({
    extends: cc.Component,
    properties: {},
    onLoad() {
        this.node.on('init', (event)=>{
            let detail = event.detail;
            // playerNode: this.playerNode,
            //     x: cc.winSize.width * 0.5 + node.width * 0.5,
            //     y: config.y,
            //     distance: config.distance

            this.playerNode = detail.playerNode;
            this.node.x = detail.x;
            this.node.y = detail.y;
            this.distance = detail.distance;
            this.node.zIndex = 0;
            this.node.dead = false;
            this.node.scale = 1;

        });
    },

    update(dt) {
        if (!global.account.gameCtl.isRunning()){
            return;
        }
        if (this.playerNode){
            this.speed = this.playerNode.getComponent('player').getNowSpeed();
            this.node.x -= this.speed * defines.timeScale * dt;

        }
        if (this.node.x + this.node.width * 0.5<  (cc.winSize.width * -0.5 - 200)){
            this.node.dead = true;
        }
    }
});